import React, { Component } from 'react'
import Web3 from 'web3';
import './css.css';
class Logo extends Component {
  

    componentDidMount() {
        window.ethereum.enable()
    }

    async my_avatar(){
        window.ethereum.enable().then(async function (accounts) {
        window.history.pushState('avatar_page', 'AVATAR PAGE', '/smartshop/?account='+accounts[0])
      })
        this.props.update("EmptyAvatar")
      }

 async jump(){
    window.ethereum.enable().then(async function (accounts) {
        
        window.history.pushState('account_page', 'ACCOUNT PAGE', '/happyfarm/?account='+accounts[0]) 
    })
    this.props.update("Empty")
 }

    render(){
        return (
        <div id="lips" opacity="1" >
            <div id="smartshop_logo"  alt="smartshop"   onClick={() => this.props.update("SmartShop")}></div><div id="lips_label" className="text white"> SmartShop</div><div id="menu_button" className="light lightblue text black">MENU</div>
            <div id="lips_menu" className="dark darkblue">
                <div className="menu_row">
                <div className="lips_menu_link lips_dir_button float_left" onClick={() => this.props.update("Book")}>BOOK<br></br><img className="lips_dir_icon"  alt="Book" src="https://i.imgur.com/pBEIDjZ.png"/></div>
                    <div className="lips_menu_link lips_dir_button float_left"     onClick={() => window.open("https://dfohub.com")}>DFO<br></br><img className="lips_dir_icon"  alt="dfo" src="https://i.imgur.com/XYKQ1X5.png"/></div>
                    <div className="lips_menu_link lips_dir_button float_left"    onClick={() => this.my_avatar()}>ACCOUNT<br></br><img className="lips_dir_icon"  alt="account" src="https://i.imgur.com/mEEm4rN.png"/></div>
                    <div className="lips_menu_link lips_dir_button float_left" onClick={() => this.props.update("EmptyShop")}>SHOP<br></br><img className="lips_dir_icon"  alt="Shop" src="https://i.imgur.com/nFrikCS.png"/></div>
                    <div className="lips_menu_link lips_dir_button float_left" onClick={() => this.props.update("Favorites")}>FAVES<br></br><img className="lips_dir_icon"  alt="Favorites" src="https://i.imgur.com/AakLdxl.png"/></div>
                    <div className="lips_menu_link lips_dir_button float_left" href="https://uniswap.info/token/0x801f90f81786dc72b4b9d51ab613fbe99e5e4ccd"   onClick={() => this.props.update("Bows")}>TOKEN<br></br><img className="lips_dir_icon"  alt="token" src="https://i.imgur.com/wlum6zh.png"/></div>                
                           
                    <div><a className="lips_menu_link lips_dir_button float_right" href="https://github.com/HappyFarm-DFO/smartshop/" target="_blank" rel="noopener noreferrer">GITHUB<br></br><img className="lips_dir_icon" alt="github" src="https://i.imgur.com/Roc9b0U.png"/></a></div>
                    <div><a className="lips_menu_link lips_dir_button float_right" href="https://discord.gg/F3Cqksx" target="_blank" rel="noopener noreferrer">DISCORD<br></br><img alt="discord" className="lips_dir_icon" src="https://i.imgur.com/tZbrqGS.png"/></a></div>
                </div>
                <div className="menu_row">
                   
                </div>
            </div>
        </div>
        );
    }
}
export default Logo