import React, { Component } from 'react'
import Web3 from 'web3'
import './css.css';



class ShopMenu extends Component {
constructor(props){
    super()
  

}


    render(props){
    console.log(props)

    
        return ( 
        <div className="Shop_row">        
            <div className="Shop_menu_panel">
            <span className="Shop_menu_item" onClick={() => this.props.setMode("userbook")}></span><span className="Shop_menu_item" onClick={() => this.props.setMode("default")}></span><span className="Shop_menu_item" onClick={() => this.props.setMode("meta")}></span><span className="Shop_menu_item" onClick={() => this.props.setMode("instructions")}></span>
               <span className="Shop_menu_item right" onClick={() => this.props.update("Book")}></span><span className="Shop_menu_item right"></span>
               <div className="clear"></div>
                     
            </div>
        </div>
            
        );
    }


}

function getUrlVars() {
    var vars = {};
    var parts = window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function(m,key,value) {
        vars[key] = value;
    });
    return vars;
}



        
const web3 = new Web3(Web3.givenProvider || "http://localhost:8545")




export default ShopMenu